.main{
    width:100%;
    height: 700px;  
    position: absolute;
    z-index: -1;
    top:0;
    background-color: #f64c72;
    clip-path: polygon(100% 0, 0 0, 0 100%);
}
.main1{
    position: absolute;
    top:0;
    right:0;
    width:100%;
    height: 700px;  
    background: #e27d60;
    clip-path: polygon(100% 0, 100% 0, 50% 100%, 0 100%);
}
.main2{
    width:15vw;
    height:200px;
    position:absolute;
    right:0;
    margin-right:20px;
    bottom:0;
    background: #2f2fa2;
    clip-path: polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%);
}
.timer{
    width:400px;
    height:200px;
    position:absolute;
    transform: translate(15%,50%);
}
#s{
    position: absolute;
    bottom:0;
    left:0;
    padding:15px;
    border-radius:2em 0em 2em 0em ;
    box-shadow: 5px 5px 5px gray;
    font-family: 14px/ 19px "adobe-clean", "Adobe Clean", sans-serif;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;;
}
.current{
transform: translate(20%,0%);
font-family: 'Courier New', Courier, monospace;
font-size: 2em;
font-weight: bold;
}
#val{
    border:2px solid black;
    border-radius: 0em 1em 0em 1em;
    box-shadow: 2px 3px 4px gray;
    transform: translate(20%,0%);
}
#val:focus{
   outline: none;
}
#e{
    position: absolute;
    bottom:0;
    
    padding:15px;
    border-radius:2em 0em 2em 0em ;
    box-shadow: 5px 5px 5px gray;
    font-family: 'Courier New', Courier, monospace;
}
